import React, { Component } from 'react';
import './App.css';
//
import Button from '@mui/material/Button';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';
import ContactMailIcon from '@mui/icons-material/ContactMail';
import SendIcon from '@mui/icons-material/Send';
import Snackbar from '@mui/material/Snackbar';
import CircularProgress from '@mui/material/CircularProgress';
import Alert from '@mui/material/Alert';
const axios = require('axios');

class App extends Component {
  constructor (props) {
    super(props)
    this.state = {
      notify:{
        open:false,
        message:""
      },
      is_loadding:false,
      mss:'',
      is_show:false
    }
  }
  componentDidMount(){
    var x = localStorage.getItem("y_time");
    if(x==null){
      this.setState({is_show:true})
    }else{
      let time=Number(x);
      let time_now=Date.now();
      let phut=(time_now-time)/60000
      if(phut>3){
        this.setState({is_show:true})
      }
    }
  }
  render() {
    let {notify,is_loadding,is_show}=this.state;
    let k='';
    if(notify.message=='success') k=<Alert severity="success">Gửi thành công!</Alert>;
    if(notify.message=='error') k=<Alert severity="error">Gửi thất bại! Thử lại.</Alert>;
    if(notify.message=='wr') k=<Alert severity="info">Bạn cần ghi nhiều hơn để chúng tôi hiểu rõ vấn đề!</Alert>;
    return (
      <React.Fragment>
        {is_show&&<div>
          {!is_loadding&&<div className='xhu'>
            <div style={{paddingLeft:'7px',position:'relative',color:'sienna',paddingBottom:'5px'}}>
              <ContactMailIcon style={{position:'absolute',top:'-3px'}}/> <span style={{marginLeft:'33px'}}>Bạn có thể đóng góp ý kiến ở đây:</span>
            </div>
            <FormControl variant="standard" sx={{ m: 1, minWidth: "100%",backgroundColor:'white' }}>
              <TextField multiline rows={8} label="Ý kiến đóng góp" 
                  value={this.state.mss}
                  onChange={(e)=>{ this.setState({mss:e.target.value}) }}
              />
            </FormControl>
            <div style={{textAlign:'right'}}
              onClick={async()=>{
                let {mss} =this.state;
                if(mss.length>20){
                  this.setState({
                      is_loadding:true
                  });
                  let data_send=new FormData();
                  data_send.append('data',JSON.stringify({comment:mss}));
                  let url=window.url;
                  //
                  await axios.post(url, 
                      data_send
                  )
                  .then( (response)=> {
                      if(response.data.status){
                        localStorage.setItem("y_time", Date.now());
                        this.setState({
                          is_show:false,
                          notify:{
                            open:true,
                            message:'success'
                          }
                        })
                      }else{
                        this.setState({
                          notify:{
                            open:true,
                            message:'error'
                          },
                          is_loadding:false
                        })
                      }
                  })
                  .catch( (error)=> {
                    this.setState({
                      notify:{
                        open:true,
                        message:'error'
                      },
                      is_loadding:false
                    })
                  })
                  //
                }else{
                  this.setState({notify:{
                    open:true,
                    message:'wr'
                  }})
                }
              }}
            >
              <Button variant="contained" ><span style={{marginRight:'5px'}} >Gửi</span><SendIcon/></Button>
            </div>
          </div>}
          {is_loadding&&<div style={{textAlign:'center'}}>
            <CircularProgress />
          </div>}
        </div>}
        <Snackbar
            open={notify.open}
            autoHideDuration={5000}
            onClose={()=>{
              let {notify}=this.state;
              notify.open=false;
              this.setState({notify:notify})
            }}
            message={notify.message}
            anchorOrigin={{  
                vertical: 'bottom',
                horizontal: 'right', 
            }}
        >
          {k}
        </Snackbar>
      </React.Fragment>
    );
  }
}

export default App;
